import React, { useState } from 'react';

// material-ui
import {
    Avatar,
    CardContent,
    CardActions,
    Card,
    Typography,
    Container,
    Chip,
    Grid,
    Button,
    TextField,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Paper,
    ListItemAvatar,
    ListItemButton,
    ListItemSecondaryAction,
    Tooltip
} from '@mui/material';
import Checkbox from '@mui/material/Checkbox';

import AddIcon from '@mui/icons-material/Add';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { makeStyles } from '@mui/styles';
import { useContext } from 'react';
import { AuthContext } from 'src/App';
import { useEffect } from 'react';
import axios from 'axios';
import { apiBaseURL } from 'src/config-global';
import { set } from 'lodash';
import { useAuthContext } from 'src/auth/useAuthContext';

// ==============================|| SAMPLE PAGE ||============================== //

const ProjectGoal = () => {
    const { user } = useAuthContext();
    const [tasks, setTasks] = useState([]);

    const greetings = ['Morning', 'Afternoon', 'Evening'];
    const date = new Date();
    const hour = date.getHours();
    const greetingIndex = hour <= 12 ? 0 : hour <= 17 ? 1 : 2;

    const fetchTasks = () => {
        axios.get(`${apiBaseURL}/manager/emptask/${user.company}?key=employee&value=${user.email}`).then((res) => {
            // console.log(res.data);
            setTasks(res.data);
        });
    };

    const handleTaskStatus = (uid, index) => (e) => {
        const status = e.target.checked;
        axios.patch(`${apiBaseURL}/manager/emptask/${uid}?status=${status}`).then((res) => {
            if (res.data === 'Updated') {
                fetchTasks();
            }
        });
    };

    useEffect(() => {
        if (!user) return;
        fetchTasks();
    }, [user]);

    return (
        <>
            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                <Grid item xs={12}>
                    <Typography variant="h3">Onboarding Tasks</Typography>
                    {/* <Typography variant="h4">Good {greetings[greetingIndex]}, {user.displayName} ðŸ˜Š</Typography> */}
                </Grid>
                {/* <Grid item xs={12}>
                    <Typography variant="h6" mx="5px">
                        Proident incididunt sunt do minim commodo. Voluptate quis cupidatat mollit ut minim voluptate qui incididunt officia
                        voluptate esse sunt anim. Mollit labore nulla quis deserunt et velit tempor Lorem aliqua. Labore est duis
                        consectetur fugiat anim duis qui laborum sint amet magna labore proident. Non tempor ut dolor ut est minim esse ut.
                        Voluptate consequat eiusmod elit nulla culpa do dolor consectetur consectetur officia non voluptate ipsum.
                    </Typography>
                </Grid> */}

                <Grid item xs={12}>
                    <TableContainer component={Paper}>
                        <Table sx={{ minWidth: 650 }} aria-label="simple table">
                            <TableHead>
                                <TableRow>
                                    <TableCell sx={{ width: '30px' }} align="center">
                                        Tasks
                                    </TableCell>
                                    <TableCell sx={{ width: '40px' }} align="center">
                                        Assigned By
                                    </TableCell>
                                    <TableCell sx={{ width: '60px' }} align="center">
                                        Due Date
                                    </TableCell>
                                    <TableCell sx={{ width: '60px' }} align="center">
                                        Source
                                    </TableCell>
                                    <TableCell sx={{ width: '60px' }} align="center">
                                        Status
                                    </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {tasks.map((item) => {
                                    return (
                                        <TableRow key={item._id} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                            <TableCell align="center" scope="row">
                                                <Typography sx={{ pt: 1 }} variant="h6">
                                                    {item.task}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="center" variant="h6">
                                                {item.manager}
                                            </TableCell>
                                            <TableCell align="center">
                                                <Typography sx={{ pt: 1 }} variant="h6">
                                                {new Date(item.due).toLocaleDateString()}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="center">
                                                <Typography sx={{ pt: 1 }} variant="h6">
                                                    <a href={`${item.url}`} target="blank">
                                                        {item.source}
                                                    </a>
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="center">
                                                <Tooltip title="Mark Completed">
                                                    <Checkbox
                                                        checked={item.status}
                                                        onChange={handleTaskStatus(item._id)}
                                                        inputProps={{ 'aria-label': 'controlled' }}
                                                        color="success"
                                                    />
                                                </Tooltip>
                                            </TableCell>
                                        </TableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
        </>
    );
};

export default ProjectGoal;
